import { AgentifaiHorizontalAlignment } from 'src/app/agentifai/models/widget-config/agentifai-horizontal-alignment';
import { AgentifaiVerticalAlignment } from 'src/app/agentifai/models/widget-config/agentifai-vertical-alignment';

export class AgentifaiWidgetDimensions {
    horizontalAlignment: AgentifaiHorizontalAlignment;
    horizontalMargin: Number;
    verticalAlignment: AgentifaiVerticalAlignment;
    verticalMargin: Number;
    size?: Number;

    constructor(
        horizontalAlignment: AgentifaiHorizontalAlignment,
        horizontalMargin: Number,
        verticalAlignment: AgentifaiVerticalAlignment,
        verticalMargin: Number,
        size?: Number
    ) {
        this.horizontalAlignment = horizontalAlignment;
        this.horizontalMargin = horizontalMargin;
        this.verticalAlignment = verticalAlignment;
        this.verticalMargin = verticalMargin;
        this.size = size;
    }
}