import { Component } from '@angular/core';
import { NavController, Platform, AlertController, LoadingController } from '@ionic/angular';
import { AgentifaiPluginControllerService } from 'src/app/services/agentifai/agentifai-plugin-controller.service';
import { PublicService } from 'src/app/services/public/public.service';
import { OpenAliceIntent } from 'src/app/agentifai/open-alice-intent';
import { InternalUtils } from 'src/app/internal/internal-utils';
import { AgentifaiWidgetDimensions } from 'src/app/agentifai/models/widget-config/agentifai-widget-dimensions';
import { AgentifaiHorizontalAlignment } from 'src/app/agentifai/models/widget-config/agentifai-horizontal-alignment';
import { AgentifaiVerticalAlignment } from 'src/app/agentifai/models/widget-config/agentifai-vertical-alignment';
import { AgentifaiServicesProvider } from 'src/app/services/agentifai/agentifai-services.service';
import { Api } from 'src/app/services/api/api.service';
import { StorageService } from 'src/app/services/storage/storage.service';
import { AssistantUtilsService } from 'src/app/services/assistant-utils/assistant-utils.service';
import { environment } from 'src/environments/environment';
import { EncryptorService } from 'src/app/services/encryptor/encryptor.service';
import { Endpoint, Intent } from 'login-lib';
import { map } from 'rxjs/operators';
import { combineLatest } from 'rxjs';

declare let Fingerprint: any;

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  endpoints = InternalUtils.endpoints as unknown as [Endpoint];
  assistantConfigsEnabled = environment.assistantConfigurationEnabled
  apiUrl = ""
  tenantId = ""
  channelId = ""
  settings: any
  themes: string[] = []
  intents = [
    { label: 'Unlock Contract', value: OpenAliceIntent.UnlockContract },
    { label: 'Moratorium Form', value: OpenAliceIntent.MoratoriumForm }
  ] as unknown as [Intent];

  constructor(
    private navCtrl: NavController,
    private alertCtrl: AlertController,
    private loadingController: LoadingController,
    private agentifaiController: AgentifaiPluginControllerService,
    private publicApi: PublicService,
    private services: AgentifaiServicesProvider,
    private platform: Platform,
    private api: Api,
    private storage: StorageService,
    private assistantUtils: AssistantUtilsService,
    private encryptor: EncryptorService
  ) {

    this.platform.ready().then(() => {
      this.readEndpoint();
      this.readAssistantConfigs();
    });

  }

  initializeAction() {
    this.assistantUtils.getConfig()
      .then(config => {
        this.agentifaiController.initializeWithConfig(config[0], config[1], config[2])
      })
  }

  showWidgetAction() {
    this.agentifaiController.showWidget();
  }

  hideWidgetAction() {
    this.agentifaiController.hideWidget()
  }

  showAppAction() {
    this.agentifaiController.showApp();
  }

  openIntent(intent: string) {
    this.agentifaiController.showApp(intent as OpenAliceIntent);
  }

  setWidgetDimensionsAction() {
    const horizontalMargin = 0;
    const verticalMargin = 30;
    const size = 60;
    const dimensions = new AgentifaiWidgetDimensions(
      AgentifaiHorizontalAlignment.Center,
      horizontalMargin,
      AgentifaiVerticalAlignment.Bottom,
      verticalMargin,
      size
    );
    this.agentifaiController.setWidgetDimensions(dimensions);
  }

  registerUser(user: any) {
    this.agentifaiController.registerUser(user.id, user.name, null, {});
  }

  unregisterUser() {
    this.agentifaiController.unregisterUser();
  }

  login(event: any) {
    const contractNumber = event.username;
    const accessCode = event.password;
    this.loginService(contractNumber, accessCode);
  }

  fakeTokenLogin(event: any) {
    const contractNumber = event.username;
    const accessCode = event.password;
    this.loginService(contractNumber, accessCode, true);
  }

  logout() {
    this.logoutService();
  }

  endpoint(event: any) {
    const endpoint = event
    this.writeEnpoint(endpoint);
    this.setConfigurations(endpoint.baseUrl);
  }

  removeVoiceConsent() {
    this.services.deleteVoiceTreatmentConsent(null)
      .subscribe(resp => {
        this.showAlert("SUCCESS", "Voice consent removed");
      }, error => {
        this.showAlert("FAILED", String(error));
      })
  }

  showNewPage() {
    this.showSecondPage();
  }

  showFingerprint() {
    Fingerprint.show({
      description: "Some biometric description"
    }, () => {
      this.showSecondPage();
    }, error => {
      alert("Authentication invalid " + error.message);
    });
  }

  setupNewTenant() {
    const apiKey = this.encodeApiKey(this.tenantId, this.channelId)
    this.assistantUtils.setConfig(this.apiUrl, apiKey, this.settings)
      .then( () => {
        this.agentifaiController.stop()
        this.initializeAction()
      })
  }

  resetDefaultTenant() {
    this.assistantUtils.resetDefaultConfig()
      .then(config => this.setupAssistantConfig(config[0], config[1], config[2]))
      .then(() => this.setupNewTenant())
  }

  setTheme(theme: string) {
    this.agentifaiController.setTheme(theme)
  }

  private showSecondPage() {
    this.navCtrl.navigateForward('second-page');
  }

  private writeEnpoint(endpoint: any) {
    this.storage.setBaseUrl(endpoint);
    this.setupBaseUrl(endpoint)
  }

  private createEndpointDefault() {
    this.writeEnpoint(this.endpoints[0]);
  }

  private readEndpoint() {
    this.storage.getbaseUrl().then(result => {
      if (result != null) {
        this.setupBaseUrl(result)
      } else {
        this.createEndpointDefault();
      }
    }, error => {
      this.createEndpointDefault();
    });
  }

  private setupBaseUrl(endpoint: any) {
    InternalUtils.baseUrl = endpoint.baseUrl;
  }

  private async showAlert(title: string, message: string) {
    let alert = await this.alertCtrl.create({
      header: title,
      message: message,
      buttons: ['Dismiss']
    });

    await alert.present()
  }

  private async loginService(contractNumber: string, accessCode: string, generateFakeToken: boolean = false) {
    const loading = (await this.getLoading())
    loading.present()

    combineLatest([
      this.publicApi.login(contractNumber, accessCode, false),
      this.encryptor.encryptRx(contractNumber)
    ])
    .pipe(
      map(results => ({ response: results[0], token: results[1] }))
    )
    .subscribe(async resp => {
      console.log(resp);
      let cookie = resp.response.headers['set-cookie']
      var authHeaders = {}
      
      if (cookie != null) {
        authHeaders = { Cookie: cookie }
      }
      
      let login = resp.response.data
      let token = generateFakeToken ? resp.token : login.jwt

      var subTitle = "Ol ";
      var name = "";
      if (login.customerName != undefined || login.customerName != null) {
        subTitle += login.customerName;
        name = login.customerName;
      } else {
        subTitle += contractNumber;
      }
      
      await this.showAlert("SUCCESS", subTitle);
      
      loading.dismiss();
      this.agentifaiController.registerUser(contractNumber, name, token, authHeaders);
    }, async error => {
      console.log(error);
      loading.dismiss()

      await this.showAlert("FAILED", error.error);
    });
  }

  private logoutService() {
    this.services.logout().subscribe((resp: any) => {
      this.showAlert("SUCCESS", "logout");
      this.unregisterUser();
    }, (error: any) => {
      this.showAlert("FAILED", error.error);
    });
  }

  private setConfigurations(endpointUrl: string) {
    let headers = this.api.getHeadersAlice();
    let headersKeysMapping = this.api.HEADERS_KEYS_MAPPING_ALICE;
    let endpoints = InternalUtils.getEndpointsWhitelist();
    this.agentifaiController.setConfigurations(
      { 
        url: endpointUrl, 
        headers: headers, 
        headersKeysMapping: headersKeysMapping,
        endpoints: endpoints
      }
    );
  }

  private readAssistantConfigs() {
    return this.assistantUtils.getConfig()
      .then(config => this.setupAssistantConfig(config[0], config[1], config[2]))
  }

  private setupAssistantConfig(apiUrl: string, apiKey: string, settings: any) {
    this.apiUrl = apiUrl
    this.settings = settings
    this.themes = Object.keys(settings["themes"])

    const decodedApiKey = this.decodeApiKey(apiKey)
    this.tenantId = decodedApiKey.tenantId
    this.channelId = decodedApiKey.channelId
  }

  private encodeApiKey(tenantId: string, channelId: String) {
    const data = {
      "tenantId": tenantId,
      "channelId": channelId
    }

    return btoa(JSON.stringify(data))
  }

  private decodeApiKey(apiKey: string) {
    return JSON.parse(atob(apiKey))
  }

  private async getLoading() {
    const loading = await this.loadingController.create({
      message: 'Please wait...',
      backdropDismiss: true,
      duration: 5000
    });

    return loading
  }

}