import { Injectable } from '@angular/core';
import { Api } from 'src/app/services/api/api.service';

@Injectable({
  providedIn: 'root'
})
export class PublicService {

  LOGIN_SERVICE: string = "system/security/authentications/basic";

  constructor(private api: Api) { }

  login(username: string, password: string, withFingerprint: boolean, deviceToken?: string) {
    let auth;
    if (withFingerprint) {
      auth = btoa(username + ":" + password + ":" + deviceToken);
    } else {
      auth = btoa(username + ":" + password);
    }

    return this.api.postLogin(auth, this.LOGIN_SERVICE + "?u=" + username + "&includeAccountsInResponse=" + "false", withFingerprint);
  }

}
